  margin: 0 auto;
}
.row {
  position: relative;
  margin-left: -15px;
  margin-right: -15px;
}
.col {
  padding-left: 15px;
  padding-right: 15px;
}
@media screen and (max-width: 48em) {
  .wrap > .row {
    margin-left: 0;
    margin-right: 0;
  }
  .col {
    margin-bottom: 15px;
  }
}
.row-bordered {
  margin-left: 1px;
  margin-right: 0;
}
.row-bordered .col {
  border: 1px solid #eeeeee;
  margin-left: -1px;
}
.row-flex {
  display: flex;
  margin-left: 1px;
  margin-right: 0;
}
.row-flex [class*=pure-u] {
  flex: 1;
  border: 1px solid #eeeeee;
  margin-left: -1px;
}
/* --------------------------------------------------------------
 * = 鼠标滑过效果
 * ----------------------------------------------------------- */
.media-hover {
  position: relative;
}
.media-hover iframe,
.media-hover .html5-video {
  display: none;
}
.media-hover:hover .media-overlay {
  background: rgba(0, 0, 0, 0.35);
}
.media-hover:hover .media-overlay span {
  background-position: 50% 50%;
  opacity: 1;
}
.media-overlay {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  background-color: #000;
  background: rgba(0, 0, 0, 0);
  cursor: pointer;
  transition: background-color 0.2s ease-out;
}
.media-overlay.default-cursor {
  cursor: default;
}
.media-overlay span {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: 50% 60%;
  opacity: 0;
  transition: all 0.15s ease-out;
}
.media-overlay .link {
  background-image: url(../images/icons/overlay-icon-link.png);
}
.media-overlay .zoom {
  background-image: url(../images/icons/overlay-icon-zoom.png);
}
.media-overlay .video {
  background-image: url(../images/icons/overlay-icon-video.png);
}
.touch-screen .media-hover .media-overlay span {
  display: none;
  background-position: 50% 50%;
}
.touch-screen .media-hover:hover .media-overlay {
  background: rgba(0, 0, 0, 0);
}
.touch-screen .flexslider.gallery .media-hover .media-overlay span,
.touch-screen .projects-carousel .media-hover .media-overlay span {
  display: block;
  background-image: none;
}
.ui-dlist {
